import React from 'react'
import Loader from 'components/Loader'

const ProviderLayout = ({ children, title, loading }) => {
  return (
    <React.Fragment>
      <div className="wrapper">
        {loading && <Loader />}
        {!loading && children}
      </div>
    </React.Fragment>
  )
}

export default ProviderLayout



